import * as React from "react";
import axios from "axios";
import "./Taskpane.css";


export const Taskpane: React.FC = () => {
  const [apiKey, setApiKey] = React.useState("");
  const [prompt, setPrompt] = React.useState("");
  const [output, setOutput] = React.useState("");

  const callAnthropic = async () => {
    try {
      const response = await axios.post(
        "https://api.anthropic.com/v1/messages",
        {
          model: "claude-3-opus-20240229",
          messages: [{ role: "user", content: prompt }],
          max_tokens: 200,
        },
        {
          headers: {
            "x-api-key": apiKey,
            "anthropic-version": "2023-06-01",
            "Content-Type": "application/json",
          },
        }
      );

      const text = response.data?.content?.[0]?.text ?? "No response from Claude.";
      setOutput(text);

      if (text.toLowerCase().includes("bold first row")) {
        await Excel.run(async (context) => {
          const sheet = context.workbook.worksheets.getActiveWorksheet();
          sheet.getRange("1:1").format.font.bold = true;
          await context.sync();
        });
      }
    } catch (error: any) {
      setOutput(`Error: ${error.message}`);
    }
  };

  return (
    <div style={{ padding: "20px", fontFamily: "Segoe UI" }}>
      <h2> speakExcel</h2>

      <label><strong>Anthropic API Key:</strong></label><br />
      <input
        type="password"
        value={apiKey}
        onChange={(e) => setApiKey(e.target.value)}
        placeholder="sk-ant-..."
        style={{ width: "100%", marginBottom: "10px" }}
      /><br />

      <label><strong>Prompt:</strong></label><br />
      <input
        type="text"
        value={prompt}
        onChange={(e) => setPrompt(e.target.value)}
        placeholder="e.g., Bold the first row"
        style={{ width: "100%", marginBottom: "10px" }}
      /><br />

      <button onClick={callAnthropic} style={{ width: "100%", marginBottom: "10px" }}>
        Run
      </button>

      <label><strong>Output:</strong></label>
      <textarea
        value={output}
        readOnly
        rows={6}
        style={{
          width: "100%",
          resize: "vertical",
          backgroundColor: "#f5f5f5",
          padding: "8px",
        }}
      />
    </div>
  );
};
